<template>
  <!--
   表单生成器的主界面
  -->
  <div class="container">
    <el-form label-width="120px">
      <el-form-item label="请求地址">
        <el-input v-model="form.url"></el-input>
      </el-form-item>
      <el-form-item label="请求方式">
        <el-select v-model="form.method" placeholder="选择请求方式">
          <el-option label="GET请求" value="GET"></el-option>
          <el-option label="POST请求" value="POST"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="ContentType">
        <el-select v-model="form.contentType" placeholder="选择ContentType类型">
          <el-option label="application/json" value="application/json"></el-option>
          <el-option label="multipart/form-data" value="multipart/form-data"></el-option>
          <el-option label="application/x-www-form-urlencoded"
                     value="application/x-www-form-urlencoded"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="请求头">
        <el-input type="textarea" v-model="form.header"></el-input>
      </el-form-item>

      <strong>配置参数</strong>

      <div v-for="param in this.form.params">
        <GenerateField :param="param"/>
      </div>

      <el-form-item>
        <el-button @click="startBuild">新增域名</el-button>
        <el-button @click="testSubmit">测试提交</el-button>
      </el-form-item>


    </el-form>


    <!--表单构建的步骤-->
    <el-dialog
      append-to-body="true"
      title="设置参数类型"
      :visible.sync="paramFieldSetVisible"
      width="700px">

      <FormBuildTypeStep ref="buildStep" @save="saveParam"/>

    </el-dialog>


  </div>


</template>

<script>
  import FormBuildTypeStep from "./FormBuildTypeStep";
  import GenerateField from "./base/GenerateField";
  import NetRequest from "../../util/code/common/NetRequest";

  export default {
    name: "FormIndex",
    components: {GenerateField, FormBuildTypeStep},
    data() {
      return {
        form: {
          url: "http://123.56.93.253:8084/pm/userLogin",
          method: "POST",
          contentType: "application/json",
          header: null,
          // param: "{\"account\":\"fang\",\"password\":\"123\"}",
          params: [],
          param:{},
        },
        paramFieldSetVisible: false,
      }
    },

    methods: {


      testSubmit() {

        this.form.params.forEach(value => {
          console.log(value[value.key])
          this.form.param[value.key] = value[value.key];
        })
        //生成表单的param
        console.log(this.form)

        NetRequest.request(this.form, (data, formItem) => {
          console.log(data);
        })


      },


      saveParam(param) {
        this.paramFieldSetVisible = false;
        localStorage.setItem("formIndex_param_item", JSON.stringify(param));
        let paramCache = JSON.parse(localStorage.getItem("formIndex_param_item"));
        this.form.params.push(paramCache);
        console.log(this.form.params)
      },

      startBuild() {
        this.paramFieldSetVisible = true;
        setTimeout(() => {
          this.$refs["buildStep"].active = 0;
          this.$refs["buildStep"].param.active = 0;

        }, 100);
      }
    }

  }
</script>

<style scoped>

  .container {
    width: 600px;
    padding-left: 10px;
  }

</style>



