<template>

  <!--
   表单生成器
  -->

  <div class="container">

    <div v-if="formVal==null" style="margin-bottom: 5px">
      <el-tooltip class="item" effect="dark" content="使用构建的历史数据构建" placement="top-start">
        <el-button type="primary" @click="histroyDialogVisible=true">使用历史</el-button>
      </el-tooltip>
    </div>

    <div class="inner-container">


      <!--
      使用外部引入的请求模块
      -->
      <el-card style="width: 800px">
        <NetCard ref="netCard" :parse-result="true" @respResult="respResult"/>
      </el-card>

      <el-form ref="form" :model="form" class="form" label-width="80px">

        <!--请求测试结束-->
        <strong>注意</strong>
        <el-tag type="success" style="margin:5px">高级选项中的
          <strong>下拉选择</strong>和<strong>范围值</strong>与<strong>是否为时间过滤</strong>
          互斥,同时选中以<strong>时间</strong>为准
        </el-tag>

        <div class="card-item-option-container">
          <el-card v-for="(keyVal,index) in tableMapper" class="card-item-option">
            <div>
              <strong>字段映射</strong>
              <el-button type="primary" style="float: right; padding: 3px 0;margin-left: 4px" @click="upMapper(index)"
                         icon="el-icon-top">上
              </el-button>
              <el-button type="primary" style="float: right; padding: 3px 0" @click="downMapper(index)"
                         icon="el-icon-bottom">下
              </el-button>
              <el-button type="primary" style="float: right; padding: 3px 0" @click="delMapper(index)">删除
              </el-button>

            </div>
            <el-form-item label="url字段">
              <el-input v-model="tableMapper[index].mname"></el-input>
            </el-form-item>
            <el-form-item label="显示字段">
              <el-input v-model="tableMapper[index].txt" placeholder="映射表字段"></el-input>
            </el-form-item>
            <el-form-item label="显示排序">

              <span>能否在表格显示</span>
              <el-switch
                v-model="tableMapper[index].isDisplay"
                active-color="#13ce66"
                inactive-color="#ff4949">
              </el-switch>
              <span class="line"></span>

              <span>能否进行排序</span>
              <el-switch
                v-model="tableMapper[index].isSort"
                active-color="#13ce66"
                inactive-color="#ff4949">
              </el-switch>
              <span class="line"></span>
            </el-form-item>

            <el-form-item label="过滤设置">
              <span>是否为过滤类型</span>
              <el-switch
                v-model="tableMapper[index].isFilter"
                active-color="#13ce66"
                inactive-color="#ff4949">
              </el-switch>
              <span class="line"></span>

              <span v-if="tableMapper[index].isFilter">是否为时间过滤</span>
              <el-switch v-if="tableMapper[index].isFilter"
                         v-model="tableMapper[index].isTimeFilter"
                         active-color="#13ce66"
                         inactive-color="#ff4949">
              </el-switch>
            </el-form-item>

            <el-form-item label="高级选项" v-if="tableMapper[index].isFilter">
              <el-radio-group v-model="tableMapper[index].option" size="small" @change="optionFilter">
                <el-radio :label="index+'_1'" border>下拉选择</el-radio>
                <el-radio :label="index+'_2'" border>范围值</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-card>
        </div>


        <strong>其他条件</strong>
        <el-form-item label="是否具有多选功能">
          <el-switch
            v-model="form.isMultSelect"
            active-color="#13ce66"
            inactive-color="#ff4949">
          </el-switch>
        </el-form-item>
        <el-form-item label="是否分页">
          <el-switch
            v-model="form.isPage"
            active-color="#13ce66"
            inactive-color="#ff4949">
          </el-switch>
        </el-form-item>
        <el-form-item label="分页大小" v-if="form.isPage">
          <el-input v-model="form.size"></el-input>
        </el-form-item>
        <el-form-item label="起始页" v-if="form.isPage">
          <el-input v-model="form.page"></el-input>
        </el-form-item>
        <strong>操作项</strong>
        <el-form-item label="操作项">
          <el-input v-model="form.oprate" placeholder="操作项,以,分割"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">立即创建</el-button>
          <el-button type="primary" @click="onGerateCode">生成代码</el-button>
          <el-button type="primary" @click="formGenerate=false">销毁</el-button>
        </el-form-item>
      </el-form>


      <TemplateTable :generate-json="generateJson" v-if="formGenerate"/>


    </div>


    <el-dialog
      title="代码生成结果"
      append-to-body="true"
      :visible.sync="generateVisable"
      width="60%">
      <div>{{generateCode}}</div>
      <span slot="footer" class="dialog-footer">
    <el-button @click="generateVisable = false">取 消</el-button>
    <el-button type="primary" @click="generateVisable = false">确 定</el-button>
  </span>
    </el-dialog>


    <!--
     历史构建
    -->
    <el-dialog
      fullscreen="true"
      append-to-body="true"
      title="预览页面"
      v-if="histroyDialogVisible"
      :visible.sync="histroyDialogVisible"
      width="90%">
      <HistroyGenerate model="select"/>
    </el-dialog>


    <el-dialog
      append-to-body="true"
      title="设置过滤条件"
      :visible.sync="optionSetFilterVisible"
      width="60%">

      <AdditionFilterStep :parse-result="false" @save="saveFilterOption"/>


    </el-dialog>


  </div>


</template>

<script>
  import axios from "axios";
  import Urls from "../../util/Urls";
  import HistroyGenerate from "./HistroyGenerate";
  import AdditionFilterStep from "./AdditionFilterStep";
  import NetRequest from "../../util/code/common/NetRequest";
  import TemplateTable from "./TemplateTable";
  import NetCard from "./NetCard";


  window.items = {};

  export default {
    name: "TableGenerator",
    components: {NetCard, AdditionFilterStep, HistroyGenerate, TemplateTable},
    data() {
      return {
        optionSetFilterVisible: false,
        optionSetFilterSelectIndex: 0,
        histroyDialogVisible: false,


        generateVisable: false,

        generateCode: null,
        //存放表单数据
        form: {},

        //存放表单映射
        tableMapper: [],

        //存放显示的tableMapper
        displayTableMapper: [],
        //表格的数据

        generateJson: null,

        formGenerate: false,

        //默认设置
        defaultSetting: null,

      }
    },

    props: [
      "formVal" //当前的作用是用来进行传递表单的数据
    ],
    methods: {

      /**
       *
       * @param resp 响应的数据
       * @param form 请求的表单
       */
      respResult(resp, form) {
        //生成映射表
        let resItem = resp.data[0];
        console.log(resItem)
        let result = []
        for (var key in resItem) {
          result.push(key);
        }
        let mappers = [];
        for (let i = 0; i < result.length; i++) {
          mappers.push({
            mname: result[i],
            isDisplay: this.form.isDesplay,
          })
        }
        this.tableMapper = mappers;

        this.form.url = form.url;
        this.form.header = form.header;
        this.form.method = form.method;
        this.form.param = form.param;
        this.form.contentType = form.contentType;
      },

      saveFilterOption(optionForm) {
        //注意这里不能直接 = 取值 ,下次调用会因为引用问题导致数据出问题,应该使用本地存储再取
        localStorage.setItem("optionForItem", JSON.stringify(optionForm));
        let optionCache = JSON.parse(localStorage.getItem("optionForItem"));
        this.tableMapper[this.optionSetFilterSelectIndex].options = optionCache;
        console.log(optionCache);
      },


      //-----
      historySelect(routerObj) {
        this.histroyDialogVisible = false;
        let obj = JSON.parse(routerObj.formVal);
        if (obj != null) {
          obj = JSON.parse(obj);
          this.form = obj.form;
          this.$refs["netCard"].form = obj.form;
          this.tableMapper = obj.tableMapper;

          console.log(obj)

          this.onSubmit();
        }
      },
      //-----历史
      upMapper(index) {
        let afterIndex = index - 1;
        let bridge = Object.create(this.tableMapper[index]);
        if (index > 0) {
          this.$set(this.tableMapper, index, this.tableMapper[afterIndex]);
          this.$set(this.tableMapper, afterIndex, bridge);
        }
      },
      downMapper(index) {
        console.log(index)
        let afterIndex = index + 1;
        let bridge = Object.create(this.tableMapper[index]);
        if (index < this.tableMapper.length - 1) {
          this.$set(this.tableMapper, index, this.tableMapper[afterIndex]);
          this.$set(this.tableMapper, afterIndex, bridge);
        }
      },

      delMapper(index) {
        this.tableMapper.splice(index, 1);
      },

      optionFilter(val) {
        let index = val.split("_")[0];
        val = val.split("_")[1];
        if (val == 1) {
          //代表需要添加
          this.optionSetFilterVisible = true;
          this.optionSetFilterSelectIndex = index;
          console.log(this.optionSetFilterSelectIndex)
        }
      },

      //提交
      onSubmit() {

        let isPass = "";
        //生成过滤项
        //生成table，根据过滤，对数据进行过滤
        let tableTitle = [];
        for (let i = 0; i < this.tableMapper.length; i++) {
          var mapper = this.tableMapper[i];
          if (mapper.isDisplay) {
            tableTitle.push(mapper);
          }

          if (mapper.isFilter) {
            if (mapper.option != undefined) {
              if (mapper.option.indexOf("_1") != -1) {
                console.log(mapper)
                if (mapper.options == null || mapper.options.dataList == null) {
                  isPass += mapper.mname + " 下拉选项缺少数据源\n";
                }
              }
            }
            if (!mapper.isTimeFilter && mapper.option == undefined) {
              //时间过滤
              isPass += mapper.mname + " 过滤类型没有指定过滤源\n";
            }
          }
        }

        if (isPass.length > 0) {
          this.$message(isPass);
          return;
        }

        this.displayTableMapper = tableTitle;

        //执行查询
        this.createCodePreview();
      },

      createCodePreview() {

        let obj = {
          form: this.form,
          tableMapper: this.displayTableMapper
        }
        this.generateJson = JSON.stringify(obj);
        this.formGenerate = true;
      },


      //代码生成
      onGerateCode() {
        this.$prompt('请输入路由名称', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({value}) => {
          //存放localStorage

          let obj = {
            form: this.form,
            tableMapper: this.displayTableMapper
          }
          let param = {
            router: value,
            formVal: JSON.stringify(JSON.stringify(obj)),
          }
          axios.post(Urls.urlRoot + "generator/router/add1", param).then((resp) => {
            //保存数据
            this.generateCode = resp.data;
            this.generateVisable = true;

          })
        }).catch(() => {

        });
      },
    },

    created() {
      this.defaultSetting = JSON.parse(localStorage.getItem("code_default_setting"));
      if (this.defaultSetting != null) {
        this.form.isPage = this.defaultSetting.isPage;
        this.form.isMultSelect = this.defaultSetting.isMultSelect;
        this.form.isDesplay = this.defaultSetting.isDesplay;
        if (this.form.isPage) {
          this.form.size = this.defaultSetting.size;
          this.form.page = this.defaultSetting.page;
        }
      } else {
        //默认选项是true
        this.form.isDesplay = true;
      }

    },

    mounted() {

    }


  }
</script>

<style scoped>

  .container {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    box-sizing: border-box;
    position: fixed;
    padding: 10px;
    overflow-x: scroll;
  }


  .inner-container {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding-right: 100px;
  }

  .netCard {
    width: 800px;
  }

  .card-item-option-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  .card-item-option {
    width: 22%;
    margin-top: 10px;
    margin-right: 20px;
    box-sizing: border-box;
    align-items: center;
  }


  .line {
    display: inline-block;
    width: 10px;
  }

</style>



