<template>
  <div>
    <div id="map_container"></div>
  </div>
</template>

<script>

  import './demo-center.css'

  export default {
    name: "Map",
    props: ["lnglat"],

    data() {
      return {
        map: null,
      }
    },
    created() {
      setTimeout(() => {

        let config = {
          resizeEnable: true, //是否监控地图容器尺寸变化
          zoom: 18, //初始化地图层级
        }

        //配置中心点坐标,为数组
        if (this.lnglat != null) {
          config.center = this.lnglat;
        }
        var map = new AMap.Map('map_container', config);

        console.log(map)

        //点击事件的触发
        map.on('click', (e) => {
          this.$emit("click", e);
        });
        this.map = map;
      }, 200);

    }

  }
</script>

<style scoped>

  #map_container {
    width: 100%;
    height: 100%;
  }


</style>



