<template>

  <!--
   附加过滤设置步骤的模板
  -->
  <div>

    <el-steps :active="active" finish-status="success">
      <el-step title="步骤 1"></el-step>
      <el-step title="步骤 2"></el-step>
      <el-step title="步骤 3"></el-step>
      <el-step title="步骤 4"></el-step>
      <el-step title="步骤 5"></el-step>
    </el-steps>


    <div v-if="active==0">

      <strong>请选择数据源来源</strong><br/><br/>
      <el-button type="primary" @click="buildDataType(2)">网络</el-button>
      <el-button type="success" @click="buildDataType(1)">本地化json</el-button>
    </div>

    <div v-if="active==1">
      <strong>请输入数据源json</strong>
      <el-form>
        <el-form-item label="数据源">
          <el-input type="textarea" v-model="form.dataList"></el-input>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="active--">上一步</el-button>
      <el-button type="primary" @click="active=4">下一步</el-button>
    </div>

    <div v-if="active==2">
      <el-button type="primary" @click="saveAutoTest(2)">保存到自动测试</el-button>
      <el-button type="primary" @click="saveAutoTest(1)">选择数据源</el-button>
      <br><br>
      <NetCard :parse-result="parseResult" ref="filterStepNetCard" @respResult="responseResult"/>
      <el-button type="primary" @click="active-=2">上一步</el-button>
      <el-button type="primary" @click="active++">下一步</el-button>

    </div>
    <div v-if="active==3">

      <el-form class="demo-form-inline">
        <el-form-item label="var data =">
          <el-input v-model="form.resp" type="textarea"></el-input>
        </el-form-item>
        <el-form-item label="数据源列表 =">
          <el-input v-model="form.parseExpressiion" placeholder="请输入解析表达式" type="textarea"></el-input>
        </el-form-item>


        <el-form-item>
          <el-button style="margin-top: 12px;" @click="active--">上一步</el-button>
          <el-button style="margin-top: 12px;" @click="nextStep">下一步</el-button>
        </el-form-item>


      </el-form>

    </div>
    <div v-if="active==4">

      <el-form label-width="120px">
        <el-form-item label="var list =">
          <el-input v-model="form.dataList" type="textarea"></el-input>
        </el-form-item>

        <el-form-item label="">
          <strong>var item =list[0]</strong>
        </el-form-item>

        <el-form-item label="选择器的Key = ">
          <el-input v-model="form.option.key" type="input"></el-input>
        </el-form-item>
        <el-form-item label="选择器的Label = ">
          <el-input v-model="form.option.label" type="input"></el-input>
        </el-form-item>

        <el-form-item label="选择器的Value = ">
          <el-input v-model="form.option.value" type="input"></el-input>
        </el-form-item>


        <el-form-item label="生成预览" v-if="dataListBack.length>0">
          <el-select placeholder="请选择" v-model="form.option.model" v-if="type==null">
            <el-option
              v-for="item in dataListBack"
              :key="item.key"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>

          <el-tag v-if="type==3" style="margin-right: 10px"
                  v-for="item in dataListBack"
                  :key="item.key">
            {{ item.label }}
          </el-tag>

          <el-radio-group v-model="form.option.model" v-if="type==9">
            <el-radio
              v-for="item in dataListBack"
              :label="item.key">{{item.label}}
            </el-radio>
          </el-radio-group>

          <div v-if="type==10">
            <el-checkbox-group v-model="form.option.models">
              <el-checkbox
                v-for="item in dataListBack"
                :label="item.label" :key="item.key">{{item.label}}
              </el-checkbox>
            </el-checkbox-group>
          </div>

        </el-form-item>


        <el-form-item>
          <el-button style="margin-top: 12px;" @click="form.type==1?active=1:active--">上一步</el-button>
          <el-button style="margin-top: 12px;" @click="preview">预览生成</el-button>
          <el-button style="margin-top: 12px;" @click="dataListBack=[]">销毁预览</el-button>
          <el-button style="margin-top: 12px;" @click="save">保存</el-button>
        </el-form-item>


      </el-form>


    </div>


    <el-drawer
      :append-to-body="true"
      title="选择保存位置"
      :visible.sync="autoTest.drawer">

      <el-tree
        v-if="autoTest.drawer"
        :props="props"
        check-strictly="true"
        :load="loadNode"
        @check="checkSave"
        lazy
        show-checkbox>
      </el-tree>


    </el-drawer>


  </div>


</template>

<script>
  import NetCard from "./NetCard";
  import axios from "axios";
  import Urls from "../../util/Urls";

  export default {
    name: "AdditionFilterStep",
    components: {NetCard},
    props: ["parseResult", "type"],
    data() {
      return {
        active: 0,

        form: {
          option: {

            models:[]

          },
          netParam: {},
        },

        autoTest: {
          drawer: false,
          model: 1, //1:选择 2:保存
        },

        dataListBack: [],

        props: {
          label: 'name',
          children: 'zones',
          isLeaf: 'leaf'
        },

      }
    },

    methods: {


      checkSave(obj, status) {


        if (this.autoTest.model === 2) {
          //保存
          this.$prompt('请输入案例描述', '案例描述', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
          }).then(({value}) => {

            axios.post(Urls.urlRoot + "autoTest/item/add", {
              autoTestId: obj.id,
              descr: value,
              type: 1,
              initCount: 1,
              innerItems: JSON.stringify([this.form.netParam])
            }).then((res) => {

            })
          }).catch(() => {

          });
        } else {
          //选择
          let forms = JSON.parse(obj.innerItems);
          this.$refs["filterStepNetCard"].form = forms[0];
          this.autoTest.drawer = false;

        }
      },


      loadNode(node, resolve) {

        if (node.level === 0) {
          axios.post(Urls.urlRoot + "autoTest/project/all").then((res) => {
            res.data.data.forEach(v => {
              v.disabled = true;
            })
            resolve(res.data.data);
          })
        } else if (node.level === 1) {

          axios.post(Urls.urlRoot + "autoTest/test/all?projectId=" + node.data.id).then((res) => {
            res.data.data.forEach(v => {
              v.leaf = this.autoTest.model != 1;
              v.disabled = this.autoTest.model == 1;
            })
            resolve(res.data.data);
          })
        } else if (node.level === 2) {
          axios.post(Urls.urlRoot + "autoTest/item/all?autoTestId=" + node.data.id).then((res) => {
            res.data.data.forEach(v => {
              v.leaf = true;
              v.name = v.descr;
            })
            resolve(res.data.data);
          })
        }
      },

      buildDataType(type) {
        //本地json type= 1
        //远程json type= 2
        this.form.type = type;
        this.active = type;
      },


      /**
       * 保存到自动测试
       */
      saveAutoTest(type) {
        this.autoTest.model = type;
        this.autoTest.drawer = true;
      },


      save() {
        this.$emit("save", this.form);

      },

      responseResult(resp, netForm) {
        this.form.resp = JSON.stringify(resp);
        this.form.netParam = netForm;
      },
      nextStep() {
        this.active++;

        let data = JSON.parse(this.form.resp);
        let dataList = eval(this.form.parseExpressiion);
        this.form.dataList = JSON.stringify(dataList);
      },

      preview() {
        let backList = [];
        let originList = JSON.parse(this.form.dataList);
        for (let i = 0; i < originList.length; i++) {
          let item = originList[i];
          var obj = {
            key: eval(this.form.option.key),
            label: eval(this.form.option.label),
            value: eval(this.form.option.value),
          }
          backList.push(obj);
        }

        console.log(backList)

        this.dataListBack = backList;
        this.form.dataListBack = backList;

      }


    }

  }
</script>

<style scoped>

</style>



