<template>
  <!--
    生成的域
  -->
  <div class="container">

    <div v-if="param.type==1">
      <el-form-item :label="param.label">
        <el-input v-model="param[param.key]" :type="param.inputType"/>
      </el-form-item>

    </div>
    <div v-if="param.type==2">

      <el-form-item :label="param.label">
        <el-select v-model="param[param.key]" placeholder="请选择">
          <el-option
            v-for="item in dataListBack"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>

    </div>
    <div v-if="param.type==3">

      <el-form-item :label="param.label">
        <el-tag style="margin-right: 10px;" :color="item.selected?'#ccc':'#fff'"
                v-for="(item,index) in dataListBack"
                :key="item.key" @click="tagSelect(item,index)">
          {{ item.label }}
        </el-tag>
      </el-form-item>


    </div>
    <div v-if="param.type==4">


      <el-form-item :label="param.label">
        <el-upload
          class="upload-demo"
          :action="param.url"
          :multiple="param.isMult"
          :data="param.addiData_parsed"
          :name="param.name"
          :on-remove="fileUploadRemove"
          :on-success="fileUploadSuccess"
          list-type="picture">
          <el-button size="small" type="primary">点击上传</el-button>
        </el-upload>
      </el-form-item>
      <el-form-item label="上传文件结果">
        <el-input v-model="uploadResult"/>
      </el-form-item>


    </div>
    <div v-if="param.type==5">

      <Editor :param="param"/>

    </div>
    <div v-if="param.type==6">

      <el-date-picker
        v-model="param[param.key]"
        type="datetime"
        :format="param.format"
        placeholder="选择日期时间">
      </el-date-picker>

    </div>
    <div v-if="param.type==7">


      <el-form-item :label="param.label+'开始'">
        <el-input :placeholder="param.label" v-model="param[param.key+'_start']"/>
      </el-form-item>
      <el-form-item :label="param.label+'结束'">
        <el-input :placeholder="param.label" v-model="param[param.key+'_end']"/>
      </el-form-item>


    </div>
    <div v-if="param.type==8">

      <Map @click="mapClick" style="width:1000px;height:500px;display: block"/>
      <el-form v-if="param.mapDisplay">
        <el-form-item :label="param.label">
          <el-input :placeholder="param.label" v-model="lnglatParam"/>
        </el-form-item>
      </el-form>


    </div>
    <div v-if="param.type==9">


      <el-form-item :label="param.label">
        <el-radio-group v-model="param[param.key]">
          <el-radio
            v-for="item in dataListBack"
            :label="item.key">{{item.label}}
          </el-radio>
        </el-radio-group>
      </el-form-item>


    </div>
    <div v-if="param.type==10">

      <el-checkbox-group v-model="checkBoxModel" @change="checkBoxChange">
        <el-checkbox
          v-for="item in dataListBack"
          :label="item.label" :key="item.key">{{item.label}}
        </el-checkbox>
      </el-checkbox-group>

    </div>
    <div v-if="param.type==11">
      <el-input-number v-model="inputNumberModel" @change="inputNumberChange" :min="param.min" :max="param.max"
                       :label="param.label"/>
    </div>
    <div v-if="param.type==12">

      <el-switch
        v-model="param[param.key1]"
        active-color="#13ce66"
        @change="switchChange"
        inactive-color="#ff4949">
      </el-switch>

    </div>
    <div v-if="param.type==13">


    </div>


  </div>


</template>

<script>
  import NetRequest from "../../../util/code/common/NetRequest";
  import Editor from "../../../util/editor/Editor";
  import Map from "./map/Map";

  export default {
    name: "GenerateField",
    components: {Map, Editor},
    props: ["param"],
    data() {
      return {

        dataListBack: [],

        uploadResult: null,
        lnglatParam: null,

        checkBoxModel: [],

        inputNumberModel: 0,
      }
    },

    methods: {

      switchChange(val) {

        if (val) {
          this.param[this.param.key] = this.param.parseTrue;
        } else {
          this.param[this.param.key] = this.param.parseFlase;
        }

      },
      inputNumberChange(currentValue, oldValue) {
        this.param[this.param.key] = currentValue;
        console.log(this.param[this.param.key])
      },

      /**
       * 绑定的是数组类型,所以只能在 回调中重新赋值
       * @param val
       */
      checkBoxChange(val) {
        this.param[this.param.key] = val;
        console.log(this.param[this.param.key])
      },

      mapClick(e) {

        this.param[this.param.key] = JSON.stringify(e.lnglat);
        this.lnglatParam = this.param[this.param.key];
      },


      fileUploadRemove(file, fileList) {
        this.fileUploadSuccess(null, file, fileList);
      },

      fileUploadSuccess(response, file, fileList) {
        let urls = [];
        fileList.forEach(value => {
          if (value.response != null) {
            let data = value.response;
            let url = eval(this.param.parse);
            urls.push(url);
          }
        })
        this.param[this.param.key] = urls.join(",");
        this.uploadResult = this.param[this.param.key];
        this.$emit("uploadResult", this.param);
      },


      parseOption(data, formItem) {
        let parseExpressiion = this.param.options.parseExpressiion;
        let originList = eval(parseExpressiion);
        this.setItemDataList(originList);
      },

      setItemDataList(originList) {
        let backList = [];
        for (let i = 0; i < originList.length; i++) {
          let item = originList[i];
          var obj = {
            key: eval(this.param.options.option.key),
            label: eval(this.param.options.option.label),
            value: eval(this.param.options.option.value),
          }
          backList.push(obj);
        }
        this.dataListBack = backList;

      },

      tagSelect(item, index) {
        if (item.selected == undefined) {
          this.$set(item, "selected", true);
          return;
        }
        this.$set(item, "selected", !item.selected);
        this.dataListBack[index] = item;

        console.log(this.param)

      }

    },

    created() {


      if (this.param.options != undefined)
        this.dataListBack = this.param.options.dataListBack;
      if (this.param.type == 2) {
        //选项类型
        if (this.param.options.type == 1) {
          return;
        }
        NetRequest.request(this.param.options.netParam,
          (data, formItem) => {
            this.parseOption(data, formItem);
          });
      } else if (this.param.type == 4) {
        this.param.addiData_parsed = eval(this.param.addiData == null ? "{}" : this.param.addiData);
      } else if (this.param.type == 11) {

        this.param.min = parseInt(this.param.min);
        this.param[this.param.key] = this.param.min;
        this.param.max = parseInt(this.param.max);

      }
    }


  }
</script>

<style scoped>

</style>



