<template>
  <!--
   表单生成器的默认设置
  -->
  <div>
    <el-form ref="form" :model="form" label-width="160px">

      <el-form-item label="选项默认显示">
        <el-switch v-model="form.isDesplay"></el-switch>
      </el-form-item>
      <el-form-item label="默认具有多选功能">
        <el-switch
          v-model="form.isMultSelect"
          active-color="#13ce66"
          inactive-color="#ff4949">
        </el-switch>
      </el-form-item>
      <el-form-item label="是否分页">
        <el-switch
          v-model="form.isPage"
          active-color="#13ce66"
          inactive-color="#ff4949">
        </el-switch>
      </el-form-item>
      <el-form-item label="分页大小" v-if="form.isPage">
        <el-input v-model="form.size"></el-input>
      </el-form-item>
      <el-form-item label="起始页" v-if="form.isPage">
        <el-input v-model="form.page"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSave">保存</el-button>
        <el-button>取消</el-button>
      </el-form-item>

    </el-form>
  </div>

</template>

<script>
  export default {
    name: "GenerateSetting",
    data() {
      return {
        form: {
          isDesplay: true,
        },
      }
    },

    methods: {

      onSave() {
        //保存到本地存储中
        localStorage.setItem("code_default_setting", JSON.stringify(this.form));
        this.$message('保存成功');
      }
    },

    created() {
      let form = JSON.parse(localStorage.getItem("code_default_setting"));
      if (form != null) {
        this.form = form;
      }

    }

  }
</script>

<style scoped>

</style>



