<script>
  import AdditionFilterStep from "../code/AdditionFilterStep";
  import GenerateField from "./base/GenerateField";

  export default {
    name: "FormBuildTypeStep",
    components: {GenerateField, AdditionFilterStep},
    data() {

      let fileResp = {
        error: 0,
        data: [
          {
            url: "/pm/imgs/aaa.png"
          }
        ],

      }

      return {
        active: 0,
        param: {
          active: 0,
          fileResp: JSON.stringify(fileResp),
        }
      }
    },

    methods: {


      uploadResult(param) {
        let result = param[param.key];

      },


      saveParam() {
        this.active++;
        this.$emit("save", this.param);
      },

      saveParamOption(optionForm) {
        //注意这里不能直接 = 取值 ,下次调用会因为引用问题导致数据出问题,应该使用本地存储再取
        localStorage.setItem("optionForItem", JSON.stringify(optionForm));
        let optionCache = JSON.parse(localStorage.getItem("optionForItem"));
        optionCache.dataListBack.forEach(value => {
          value.selected = false;
        })
        this.param.options = optionCache;
        this.active++;
      }


    }

  }
</script>

<style scoped>

  .select-build-type-container {
