<template>


  <div class='generateStart'>

    <div class='tableFilter'>
      <el-form :inline='true'>
        <el-form-item :label='mapper.txt' v-for='mapper in tableMapper' v-if='mapper.isFilter'>
          <el-input v-model='mapper[mapper.mname]' :clearable="true"
                    v-if='!mapper.isTimeFilter&&mapper.option==undefined'
                    :placeholder='mapper.txt'></el-input>

          <div v-if='mapper.isTimeFilter'>
            <el-col :span='11'>
              <el-date-picker type='date'
                              value-format='yyyy-MM-dd'
                              v-model='mapper.startTime'
                              style='width: 100%;'></el-date-picker>
            </el-col>
            <el-col style='width: 20px;text-align:center' :span='2'>-</el-col>
            <el-col :span='11'>
              <el-date-picker type='date'
                              value-format='yyyy-MM-dd'
                              v-model='mapper.endTime'
                              style='width: 100%;'></el-date-picker>
            </el-col>
          </div>

          <div v-else-if="mapper.option.indexOf('_1')!=-1">
            <el-select v-model="mapper[mapper.mname]" placeholder="请选择" @change="handleOptionChange">
              <el-option
                v-for="item in mapper.options.netParam.dataListBack"
                :key="item.key"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>

          <div v-else-if="mapper.option.indexOf('_2')!=-1">

            <el-col :span="11">
              <el-input v-model="mapper[mapper.mname+'_start']"
                        :placeholder="mapper.txt+'开始'"></el-input>
            </el-col>
            <el-col style="width: 20px;text-align:center" :span="2">-</el-col>
            <el-col :span="11">
              <el-input v-model="mapper[mapper.mname+'_end']"
                        :placeholder="mapper.txt+'结束'"></el-input>
            </el-col>
          </div>

        </el-form-item>
        <el-button type='primary' @click='onFilterQuery'>查询</el-button>
        <el-button type='primary' @click='onClearFilter'>清除</el-button>

      </el-form>
    </div>

    <div class='tableGenerator'>

      <el-table
        ref='filterTable'
        @sort-change='tableSortChange'
        :data='tableData'
        @selection-change='handleSelectionChange'
        style='width: 100%'>

        <el-table-column v-if='form.isMultSelect'
                         type='selection'
                         width='55'/>
        <el-table-column v-for='mapper in tableMapper'
                         :sortable='mapper.sort'
                         :prop='mapper.mname'
                         :label='mapper.txt'>
        </el-table-column>

        <el-table-column label='操作'>
          <template slot-scope='scope'>
            <el-button v-for='(operate,index) in form.operates' @click='operateFunc(scope.row,operate)'
                       size='mini'>{{operate}}
            </el-button>
          </template>
        </el-table-column>

      </el-table>
      <el-pagination v-if='form.isPage&&form.size>0'
                     @current-change='handleCurrentChange'
                     :page-size='form.size'
                     layout='prev, pager, next, jumper'
                     :total='form.total'>
      </el-pagination>

    </div>
  </div>


</template>

<script>

  import axios from 'axios';
  import NetRequest from "../../util/code/common/NetRequest";

  export default {

    data() {
      return {
        form: {},
        tableMapper: [],
        tableData: [],
        selectOptionsMapper: [],
      }
    },

    props: [
      "generateJson",
    ],

    methods: {

      handleOptionChange(val) {
        console.log(val)
      },


      onFilterQuery() {
        this.form.param.page = this.form.page;
        this.onQuery();
      },

      onClearFilter() {
        for (let i = 0; i < this.tableMapper.length; i++) {
          var mapper = this.tableMapper[i];
          mapper[mapper.mname] = '';
        }
      },

      callOption() {
        for (let index = 0; index < this.selectOptionsMapper.length; index++) {
          this.selectOptionsMapper[index].options.netParam.index =
            this.selectOptionsMapper[index].index;

          let type = this.selectOptionsMapper[index].options.type;
          if (type == undefined) {
            type = 2;
          }

          if (type == 1) {
            let itemMapper = this.selectOptionsMapper[index];
            itemMapper.options.netParam.dataListBack = itemMapper.options.dataListBack;
            this.$set(this.tableMapper, this.selectOptionsMapper[index].index, itemMapper);
            continue;
          }
          NetRequest.request(this.selectOptionsMapper[index].options.netParam,
            (data, formItem) => {
              this.parseOption(data, formItem);
            });
        }
      },

      parseOption(data, formItem) {
        let itemMapper = this.tableMapper[formItem.index];
        let parseExpressiion = itemMapper.options.parseExpressiion;
        let originList = eval(parseExpressiion);
        this.setItemDataList(originList, itemMapper, formItem.index);
      },


      setItemDataList(originList, itemMapper, index) {
        let backList = [];
        for (let i = 0; i < originList.length; i++) {
          let item = originList[i];
          var obj = {
            key: eval(itemMapper.options.option.key),
            label: eval(itemMapper.options.option.label),
            value: eval(itemMapper.options.option.value),
          }
          backList.push(obj);
        }
        itemMapper.options.netParam.dataListBack = backList;
        this.$set(this.tableMapper, index, itemMapper);

      },


      onQuery() {
        let param = this.form.param;
        for (var i = 0; i < this.tableMapper.length; i++) {
          var mapper = this.tableMapper[i];
          if (mapper.isFilter) {
            if (mapper.isTimeFilter) {
              let timestart = mapper.mname + '_start';
              let timeend = mapper.mname + '_end';
              param[timestart] = mapper['startTime'];
              param[timeend] = mapper['endTime'];
            } else if (mapper.option.indexOf("_1") != -1) {
              param[mapper.mname] = mapper[mapper.mname];
            } else if (mapper.option.indexOf("_2") != -1) {
              let itemstart = mapper.mname + "_start";
              let itemend = mapper.mname + "_end";
              param[itemstart] = mapper[itemstart];
              param[itemend] = mapper[itemend];

            } else {
              param[mapper.mname] = mapper[mapper.mname];
            }
          }
        }
        for (var key in param) {
          if (param[key] == '') {
            delete param[key];
          }
        }
        if (this.form.isPage) {
          if (param.page == undefined) {
            param.page = this.form.page;
          }
          param.size = this.form.size;
        }
        this.request(this.form);
      },

      request(item) {
        let method = item['method'];
        if (method == 'GET') {
          this.getRequest(item);
        } else if (method == 'POST') {
          this.postRequest(item);
        }
      },

      postRequest(item) {
        let url = item['url'];
        axios.post(url, item.param, {
          headers: item.header,
        }).then((resp) => {
          let data = resp.data;
          this.parseResult(data);
        })
      },

      getRequest(item) {
        let url = item['url'];
        axios.get(url, {
          headers: item.header,
          data: item.param
        }).then((resp) => {
          this.processResult(resp, item);
        })
      },

      parseResult(netResp) {
        this.form.total = netResp.total;
        this.tableData = netResp.data;
      },


      tableSortChange(obj) {
        let param = this.form.param;
        param.order = obj.order;
        param.prop = obj.prop;
        this.onQuery();
      },

      handleCurrentChange(page) {
        let param = this.form.param;
        param.page = page;
        param.size = this.form.size;
        this.onQuery();
      },


      handleSelectionChange(selection) {
        console.log(selection);
      },
      operateFunc(obj, operate) {
        console.log(obj, operate);
      }
    },

    created() {

      var obj = this.generateJson;
      if (obj == null) {
        obj = "__template__";
      }
      obj = JSON.parse(obj);
      console.log(obj)
      this.form = obj.form;
      let paramExpression;
      eval("paramExpression=" + this.form.param);
      this.form.param = paramExpression;
      let headerExpression;
      eval("headerExpression=" + this.form.header);
      this.form.header = headerExpression;

      let operates = this.form.oprate.split(',');
      this.form.operates = operates;

      let localTableMapper = obj.tableMapper;
      for (var i = 0; i < localTableMapper.length; i++) {
        let mapper = localTableMapper[i];
        mapper.sort = mapper.isSort ? 'custom' : false;
        if (mapper.option != undefined && mapper.isFilter) {
          if (mapper.option.indexOf("_1") != -1) {
            mapper.index = i;
            this.selectOptionsMapper.push(mapper);
          }
        }
      }
      if (this.selectOptionsMapper.length > 0) {
        console.log(this.selectOptionsMapper)
        this.callOption();
      }
      this.tableMapper = localTableMapper;
      this.onQuery();
    }


  }
</script>

<style scoped>


</style>



