import axios from "axios";


let netRequestParam = {};
window.netRequestParam = netRequestParam;


//网络执行－－－－－－－－－－－－－－－
/**
 * 请求参数的封装类
 * @param item
 */
function request(item, callback) {

  //存放原始的表達式
  localStorage.setItem("net_request_generate_code_param", item.param);
  localStorage.setItem("net_request_generate_code_header", item.header);

  let method = item["method"];
  if (method == "GET") {
    getRequest(item, callback);
  } else if (method == "POST") {
    postRequest(item, callback);
  }
}

function commonProcess(item) {
  //这里使用eval的意义是，可能下面的几个参数存在参数运算
  if (item["header"] != null && item["header"].length > 0) {
    eval("window.netRequestParam['header']=" + item["header"]);
  }
  if (item["param"] != null && item["param"].length > 0) {
    eval("window.netRequestParam['param']=" + item["param"])
  }
  //请求的ContentType
  if (netRequestParam["header"] != undefined) {
    netRequestParam["header"]["Content-Type"] = item.contentType;
  }

  //转存到form中
  item.param = JSON.stringify(window.items['param']);
  item.header = JSON.stringify(window.items['header']);

}


function getRequest(item, callback) {
  //定义上一个的响应对象
  let url = item["url"];
  commonProcess(item);
  axios.get(url, {
    headers: JSON.parse(item.param),
    data: JSON.parse(item.param)
  }).then((resp) => {
    processResult(resp, item, callback);
  })
}

function postRequest(item, callback) {
  let url = item["url"];
  commonProcess(item);
  axios.post(url, JSON.parse(item.param == null ? "{}" : item.param), {
    headers: JSON.parse(item.header == null ? "{}" : item.header),
  }).then((resp) => {
    processResult(resp, item, callback);
  })
}


function processResult(resp, item, callback) {
  let data = resp.data;
  netRequestParam.netResp = data;
  item.param = localStorage.getItem("net_request_generate_code_param");
  item.header = localStorage.getItem("net_request_generate_code_header");
  //返回响应结果
  callback(data, item);
}


export default {
  request, getRequest, postRequest, commonProcess, netRequestParam
}




