<template>

  <!--
    项目的主页
  -->
  <el-container>
    <el-aside width="200px">

      <div style="position: absolute;height: 100%;background-color:#545c64; width: 199px"/>


      <el-menu default-active="1"
               background-color="#545c64"
               text-color="#fff"
               active-text-color="#ffd04b">

        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-s-goods"></i>
            <span>软件管理</span>
          </template>
          <el-menu-item index="1-1" @click="changeRouter($event)" data-route="/pm">软件包管理</el-menu-item>
          <el-menu-item index="1-2" @click="changeRouter($event)" data-route="/routeChange">路由控制</el-menu-item>
          <el-menu-item index="1-3" @click="changeRouter($event)" data-route="/LogcatQuery">日志分析</el-menu-item>
        </el-submenu>


        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-warning"></i>
            <span>代码检查</span>
          </template>
          <el-menu-item index="2-1" @click="changeRouter($event)" data-route="/UserBehavior">Lint代码检查</el-menu-item>
          <el-menu-item index="2-2" @click="changeRouter($event)" data-route="/CheckReport">依赖检查报告</el-menu-item>
        </el-submenu>

        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-s-help"></i>
            <span>表格生成器</span>
          </template>
          <el-menu-item index="3-1" @click="changeRouter($event)" data-route="/TableGenerator">表格生成器</el-menu-item>
          <el-menu-item index="3-2" @click="changeRouter($event)" data-route="/HistroyGenerate">生成记录</el-menu-item>
          <el-menu-item index="3-3" @click="changeRouter($event)" data-route="/GenerateSetting">默认设置</el-menu-item>
          <el-menu-item index="3-4" @click="changeRouter($event)" data-route="/Test">测试动态</el-menu-item>
        </el-submenu>

        <el-submenu index="5">
          <template slot="title">
            <i class="el-icon-s-help"></i>
            <span>表单生成器</span>
          </template>
          <el-menu-item index="6-1" @click="changeRouter($event)" data-route="/FormIndex">表单生成器</el-menu-item>
          <el-menu-item index="6-2" @click="changeRouter($event)" data-route="/FormHistory">生成记录</el-menu-item>
          <el-menu-item index="6-3" @click="changeRouter($event)" data-route="/FormSetting">默认设置</el-menu-item>
          <el-menu-item index="6-4" @click="changeRouter($event)" data-route="/FormTest">测试动态</el-menu-item>
        </el-submenu>

        <el-submenu index="4">
          <template slot="title">
            <i class="el-icon-s-unfold"></i>
            <span>自动测试</span>
          </template>
          <el-menu-item index="4-1" @click="changeRouter($event)" data-route="/TestProject">测试项目</el-menu-item>
          <el-menu-item index="4-2" @click="changeRouter($event)" data-route="/ComplexMultTaskPipeline">多任务流水线
          </el-menu-item>
        </el-submenu>

        <el-submenu index="5">
          <template slot="title">
            <i class="el-icon-menu"></i>
            <span>Crm内容管理</span>
          </template>
          <el-menu-item index="5-1" @click="changeRouter($event)" data-route="/CrmMenu">Crm菜单设置</el-menu-item>
          <el-menu-item index="5-2" @click="changeRouter($event)" data-route="/CrmPreViewer">Crm内容预览</el-menu-item>
        </el-submenu>

        <el-menu-item @click="logout" type="danger">退出登录</el-menu-item>

      </el-menu>
    </el-aside>

    <el-container>
      <!--
       路由的主页面
      -->
      <router-view/>

    </el-container>
  </el-container>


</template>

<script>

  import Urls from "../util/Urls";
  import axios from "axios";

  export default {
    name: "Index",
    data() {
      return {
        index: 1,
      }
    },

    methods: {

      changeRouter(event) {
        this.$router.push(event.$el.dataset['route']);
      },

      logout() {

        axios.post(Urls.urlRoot + "logout").then(res => {
          this.$router.replace("/Login");
        })


      }

    },

    created() {
      this.$router.push("/pm");
    }


  }
</script>

<style scoped>

  .el-menu {
    border-right: none;
  }

</style>



