<template>
  <div id="editor_container">
  </div>
</template>

<script>

  import "./js/wangEditor.min";

  export default {
    name: "Editor",
    props: ["param"],
    data() {
      return {
        editor: null,
      }
    },

    created() {
      setTimeout(() => {
        var E = window.wangEditor
        var editor = new E('#editor_container')

        console.log(this.param);

        editor.config.uploadImgServer = this.param.url
        editor.config.uploadVideoServer = editor.config.uploadImgServer;
        editor.config.uploadFileName = this.param.name
        editor.config.uploadImgHeaders = eval(this.param.header)
        editor.create()
        this.editor = editor;
      }, 500);
    },

    methods: {

      getHtml() {
        return this.editor.txt.html();
      },
      getTxt() {
        return this.editor.txt.text();
      },

      setHtml(html) {
        this.editor.txt.html(html);
      },

      appendHtml(html) {
        this.editor.txt.append(html);
      }


    }


  }
</script>

<style scoped>

  #editor_container{
    z-index: 3;
    position: relative;
  }

  >>.w-e-toolbar{
    z-index: 3;
  }
  >>.w-e-text-container{
    z-index: 2;
  }

</style>



